          if (Array.isArray(src[i])) dst[i] = src[i].slice();
          else if (typeof(src[i])==='object') {
            if (src[i]===null) dst[i] = null;
            else dst[i] = this.deep_copy({},src[i]);
          } else dst[i] = src[i];
      return dst;
