////      var offset_top = 0;
//      var marked_first_post = null;
//      for (var i=posts.length-1;i>=0;i--) {
////        var reply = posts[i];
//////        posts[i] = posts[i].parentNode;
//////        if (class_or_tag=='class') while (posts[i].className.search(key)==-1) posts[i] = posts[i].parentNode;
