        if (cmd!=='UNDO') for (var i=names.length-1;i>=1;i--) this.triage_exe_broadcast(names[i],cmd,attr, hist? 'multi' : hist, null, null, null, true);
        this.triage_exe_broadcast(names[0],cmd,attr,hist, datetime);
