////        while (j<data.length && !data[j]) j++;
////        if (j_old+1===j) continue;
////        if (j<data.length) {
//////console.log('setup_board_no: averaging: '+j_old+', '+j);
////          var step = (data[j]-data[j_old])/(j-j_old);
////          for (var i=1;i<j-j_old;i++) data[j_old+i] = data[j_old]+Math.floor(step*i);
////        }
